import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';

import { HeaderComponent } from './header/header.component';


@NgModule({
  declarations: [
    HeaderComponent,
  ],
  imports: [
    // Angular 2 related modules
    HttpModule,
  ],
  exports: [
    HeaderComponent,
  ]
})
export class HeaderModule { }



